body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.yellow-color {
  color: rgb(226, 200, 8) !important;
}

.green-color {
  color: green !important;
}

.red-color {
  color: red !important;
}

.search-box {
  border-radius: 20px;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-box > input[type="text"] {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: rgb(231, 231, 231);
  border: none;
  color: rgba(0, 0, 0, 0.637);
  padding: 1vmax 1.5vmax;
  width: 90%;
  outline: none;
  font: 300 1vmax "Roboto";
  box-sizing: border-box;
  height: 45px;
}

.search-box > input[type="submit"] {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  height: 45px;
  background-color: #ffc000;
  border: none;
  padding: 0.5vmax;
  width: 15%;
  font: 300 1.1vmax "Roboto";
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}

.search-box > input[type="submit"]:hover {
  background-color: tomato;
}

@media screen and (max-width: 600px) {
  .search-box > input[type="text"] {
    font-size: 12px;
    width: 100%;
    font: 300 3vw "Roboto";
    padding: 0 15px;
    height: 30px;
  }

  .search-box > input[type="submit"] {
    height: 30px;
    width: 35%;
    font: 300 3vw "Roboto";
  }
}

.loading {
  width: 100vw;
  height: 100vh;
  background-color: white;
  display: grid;
  place-items: center;
  max-width: 100%;
}

.loading div {
  width: 10vmax;
  height: 10vmax;
  border-bottom: 5px solid rgba(0, 0, 0, 0.719);
  border-radius: 50%;
  -webkit-animation: loadingRotate 800ms linear infinite;
          animation: loadingRotate 800ms linear infinite;
}

@-webkit-keyframes loadingRotate {
  to {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
  }
}

@keyframes loadingRotate {
  to {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
  }
}

.footer {
  margin-top: 10vmax;
  padding: 2vmax;
  background-color: rgb(34, 33, 33);
  color: white;
  display: flex;
  align-items: center;
}

.left-footer {
  width: 20%;
  display: flex;
  flex-direction: column;
}

.left-footer > h3 {
  font-family: "Roboto";
  font-size: 1.2vmax;
  text-align: center;
  padding: 2vmax;
}

.footer-social-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0 2.3vmax 2vmax;
}

.footer-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 25px;
  transition: all 0.5s;
  cursor: pointer;
}

.footer-icon:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.facebook {
  background-color: #1278f3;
}

.facebook:hover {
  background-color: black;
  border: 1px solid #1278f3;
  color: #1278f3;
}

.instagram {
  background-color: #d32598;
}

.instagram:hover {
  background-color: black;
  border: 1px solid #d32598;
  color: #d32598;
}

.twitter {
  background-color: #1d9bf0;
}

.twitter:hover {
  background-color: black;
  border: 1px solid #1d9bf0;
  color: #1d9bf0;
}

.youtube {
  background-color: #fe0000;
}

.youtube:hover {
  background-color: black;
  border: 1px solid #fe0000;
  color: #fe0000;
}

.middle-footer {
  width: 60%;
  text-align: center;
}

.middle-footer > h1 {
  font-size: 3.2vmax;
  font-family: "Roboto";
  color: #eb4034;
  padding-bottom: 10px;
}

.middle-footer > h3 {
  font-size: 1.8vmax;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: #fff200f2;
  transition: all 0.5s;
}

.middle-footer > h3:hover {
  -webkit-transform: scaleX(1.15);
          transform: scaleX(1.15);
}

.footer-desc {
  max-width: 60%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 1vmax auto;
  font-size: 1vmax;
}

.right-footer {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.right-footer > h3 {
  font-family: "Roboto";
  font-size: 1.2vmax;
  padding-top: 2vmax;
}

.right-footer > a {
  text-decoration: none;
  font-size: 1.2vmax;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: white;
  margin: 0.5vmax;
  display: flex;
  padding: 0.8vmax 0.2vmax 0.3vmax 0;
  align-items: center;
}

.email-icon {
  background-color: white;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f44336;
}

.right-footer > a {
  transition: all 0.5s;
  cursor: pointer;
}

.right-footer > a:hover {
  color: #f44336;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.right-footer > img {
  padding-top: 0.8vmax;
}

@media screen and (max-width: 600px) {
  .footer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
  }

  .middle-footer {
    width: 100vw;
    height: 100%;
    position: relative;
  }

  .middle-footer > h1 {
    font-size: 3vmax;
  }

  .middle-footer > h3 {
    font-size: 3vmax;
    margin: 10px 0;
    text-align: center;
  }

  .footer-desc {
    font-size: medium;
    text-align: center;
    max-width: unset;
  }

  .copyrighted {
    position: absolute;
    bottom: 0;
    font-size: medium;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }

  .left-footer {
    width: auto;
    position: absolute;
    left: auto;
    right: auto;
    -webkit-transform: translateY(3vh);
            transform: translateY(3vh);
    padding: 10px;
  }

  .left-footer > h3 {
    font-size: 2.8vmax;
  }

  .right-footer {
    padding-top: 10px;
    position: absolute;
    left: auto;
    right: auto;
    -webkit-transform: translateY(110%);
            transform: translateY(110%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
  }

  .right-footer > h3 {
    font-size: 2.8vmax;
  }

  .right-footer > a {
    width: auto;
    padding-bottom: 8px;
  }
  .right-footer > a > h4 {
    font-size: medium;
  }
}

.banner {
  background-image: url(/static/media/bgimg.2d2cbdec.jpg),
    linear-gradient(to right, #635dc0, #3027ae);
  background-position: center;
  background-size: cover;
  height: 100vmin;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  position: relative;
}

.banner h1 {
  margin: 5vmax;
  font: 600 3.5vmax "Roboto";
}

.banner p {
  font: 300 2.5vmax "Lucida Sans";
}

.banner button {
  margin-bottom: 5vmax;
  cursor: pointer;
  background-color: white;
  border: 1px solid white;
  border-radius: 0;
  padding: 1vmax;
  transition: all 0.5s;
  width: 9vmax;
  font: 500 1vmax "Roboto";
}

.banner button:hover {
  background-color: rgba(255, 255, 255, 0);
  color: white;
}

.banner::after {
  content: "";
  width: 100vw;
  height: 100vmin;
  background-color: #ffffff;
  position: absolute;
  top: 0%;
  left: 0;
  -webkit-clip-path: polygon(100% 68%, 0 100%, 100% 100%);
          clip-path: polygon(100% 68%, 0 100%, 100% 100%);
  max-width: 100%;
}

.home-heading {
  text-align: center;
  font-family: Roboto;
  font-size: 1.4vmax;
